/* General styles for components in the form section */
.cv-form {
    padding: 1rem;
    display: flex;
    flex-flow: column nowrap;

    align-items: center;
    gap: 15px;

    min-height: 100vh;
}

.form-div {
    width: 90%;
}

.user-form {
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    padding: 0.8rem;

    background-color: var(--formBg);

    gap: 8px;
    height: max-content;
    border-radius: 1rem;
}

.addBtn {
    border: 0;
    padding: 10px 1rem;
    border-radius: 0.5rem;
    background-color: var(--inputBg);

    color: var(--myGreen);
    font-size: 1.2rem;
    width: 100px;
    display: block;
}

.addBtn:hover {
    background-color: var(--myGreen);
    color: var(--inputBg);
}

.btn-holder {
    display: flex;
    justify-content: center;

}